"use client";

import { useState, useEffect, useRef, useCallback } from "react";
import { useQuery } from "@tanstack/react-query";
import dynamic from "next/dynamic";

import IncidentMainTable from "@/features/incidents/components/IncidentMainTable";
import IncidentsHeader from "@/features/incidentsHeader/incidentsHeader";
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { useIncidents } from "@/hooks/useIncidentQueries";
import { IncidentForm } from "@/types/incidentModal";
import { PrintWrapper } from "@/components/PrintWrapper";

// ✅ Lazy-load the modal for performance
const IncidentModal = dynamic(
  () => import("@/features/incidentsModal/components/IncidentModal"),
  { ssr: false }
);

// ✅ Simple reusable debounce hook
function useDebounce<T>(value: T, delay = 500): T {
  const [debounced, setDebounced] = useState(value);
  useEffect(() => {
    const handler = setTimeout(() => setDebounced(value), delay);
    return () => clearTimeout(handler);
  }, [value, delay]);
  return debounced;
}

type Role = "admin" | "user";
export type Organization = "PTC" | "GICC" | "ALL";

// ✅ Typed user response
interface MeResponse {
  user: {
    role: string;
    organization: string;
    email: string;
  };
}

// ✅ User fetcher for React Query
const fetchMe = async (): Promise<MeResponse> => {
  const res = await fetch("http://localhost:5000/api/auth/me", {
    credentials: "include",
  });
  if (!res.ok) {
    if (res.status === 401 && typeof window !== "undefined") {
      window.location.href = "/login";
    }
    throw new Error("Failed to fetch user");
  }
  return res.json();
};

const defaultForm: IncidentForm = {
  refNo: 0,
  dateReported: undefined,
  description: "",
  natureOfException: "",
  auditFinding: "",
  reportingDepartment: "",
  reportingEmployee: "",
  concernType: "supplier",
  concernName: "",
  customerDepartment: "",
  moduleOfPurchase: "Imported",
  typeOfDelivery: "indent",
  classification: "",
  status: "",
  date: undefined,
  incidentDetails: [],
  incidentIssues: [],
  incidentIssuesSelection: {},
  incidentActions: [
    {
      tempId: crypto.randomUUID(),
      correction: [],
      corrective: [],
      occurence: "",
      consequence: "",
      rootCause: "",
      analysis: [],
    },
  ],
  incidentEvaluation: [
    {
      tempId: crypto.randomUUID(),
      effectiveness: [],
      documentation: [],
    },
  ],
  organization: "PTC",
};

export default function HomePage() {
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [selectedOrg, setSelectedOrg] = useState<Organization>("ALL");
  const [isOpen, setIsOpen] = useState(false);
  const [editingIncident, setEditingIncident] = useState<
    (IncidentForm & { _id: string }) | null
  >(null);
  const [form, setForm] = useState<IncidentForm>(defaultForm);
  const [isModalDisabled, setIsModalDisabled] = useState(false);

  const [search, setSearch] = useState("");
  const debouncedSearch = useDebounce(search);

  // ✅ Use React Query (v5-compliant syntax)
  const {
    data: me,
    error: meError,
    isLoading: meLoading,
  } = useQuery<MeResponse>({
    queryKey: ["me"],
    queryFn: fetchMe,
  });

  const role = (me?.user?.role === "admin" ? "admin" : "user") as Role;
  const userOrg = (
    ["PTC", "GICC", "ALL"].includes(me?.user?.organization ?? "")
      ? (me?.user?.organization as Organization)
      : "PTC"
  ) as Organization;

  // ✅ Auto-set org based on user
  useEffect(() => {
    if (!meLoading && !meError) {
      if (role !== "admin" && userOrg) {
        setSelectedOrg(userOrg === "ALL" ? "ALL" : (userOrg as Organization));
      }
    }
  }, [role, userOrg, meLoading, meError]);

  // ✅ Fetch incidents with debounced search
  const {
    data: incidents,
    isLoading,
    isError,
  } = useIncidents(currentPage, itemsPerPage, selectedOrg, debouncedSearch);

  const componentRef = useRef<HTMLDivElement>(null);
  const dialogRef = useRef<HTMLDivElement>(null);
  const [isPrinting, setIsPrinting] = useState(false);
  const [printWindow, setPrintWindow] = useState<Window | null>(null);

  // ✅ Optimized print handler
  const handlePrint = useCallback(() => {
    try {
      if (printWindow) {
        printWindow.close();
        setPrintWindow(null);
      }
      setIsPrinting(true);
      const content = componentRef.current;
      if (!content) return;

      setTimeout(() => {
        const pw = window.open("", "_blank");
        if (!pw) return;
        setPrintWindow(pw);

        pw.document.write(`
          <!DOCTYPE html>
          <html>
            <head>
              <title>Print Report</title>
              <style>
                @page { size: A4; margin: 20mm; }
                body {
                  font-family: system-ui, -apple-system, sans-serif;
                  line-height: 1.5;
                  color: black;
                  background: white;
                  margin: 0;
                  padding: 20mm;
                }
                * {
                  print-color-adjust: exact !important;
                  -webkit-print-color-adjust: exact !important;
                }
                table { width: 100%; border-collapse: collapse; }
                th, td { border: 1px solid black; padding: 8px; }
                th { background-color: #f3f4f6 !important; }
              </style>
            </head>
            <body>${content.outerHTML}</body>
          </html>
        `);

        pw.document.close();
        pw.onload = () => {
          pw.focus();
          pw.print();
        };
      }, 100);
    } catch (error) {
      console.error("Print error:", error);
      setIsPrinting(false);
    }
  }, [printWindow]);

  // ✅ Cleanup after print
  useEffect(() => {
    const cleanup = () => {
      if (printWindow) {
        printWindow.close();
        setPrintWindow(null);
      }
      setIsPrinting(false);
    };
    const handleFocus = () => {
      if (printWindow?.closed) cleanup();
    };
    window.addEventListener("focus", handleFocus);
    return () => {
      window.removeEventListener("focus", handleFocus);
      cleanup();
    };
  }, [printWindow]);

  // ✅ Handlers
  const handleRowClick = useCallback(
    (incident: IncidentForm & { _id: string }) => {
      setEditingIncident(incident);
      setForm({
        ...incident,
        date: incident.date ? new Date(incident.date) : undefined,
        organization: incident.organization,
      });
      setIsOpen(true);
    },
    []
  );

  const handleCreate = useCallback(() => {
    setEditingIncident(null);
    setForm(defaultForm);
    setIsOpen(true);
  }, []);

  const handleClose = useCallback(() => {
    setForm(defaultForm);
    setEditingIncident(null);
    setIsOpen(false);
  }, []);

  if (meLoading) return <p>Loading user...</p>;
  if (meError) return <p className="text-red-500">Failed to fetch user info</p>;

  return (
    <div className="h-screen flex flex-col dark:bg-gradient-to-r dark:from-sky-950 dark:to-sky-800 dark:text-white">
      {/* Hidden Print Container */}
      <div style={{ display: "none" }}>
        <div ref={componentRef}>
          {editingIncident && <PrintWrapper incident={form} />}
        </div>
      </div>

      <IncidentsHeader
        selectedOrg={selectedOrg}
        onSelectOrg={setSelectedOrg}
        role={role}
        userOrg={userOrg}
        email={me?.user?.email ?? ""}
      />

      <div className="p-6 overflow-y-auto flex-1">
        <div className="flex justify-between items-center mb-4">
          <h1 className="text-red-600 text-lg">Incident Reports</h1>

          {/* Debounced Search */}
          {(role === "admin" || userOrg === "ALL") && selectedOrg === "ALL" && (
            <input
              type="text"
              placeholder="Search by Ref No. or Description..."
              value={search}
              onChange={(e) => {
                setSearch(e.target.value);
                setCurrentPage(1);
              }}
              className="border border-gray-300 dark:border-gray-700 rounded-lg px-3 py-2 text-sm w-72 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          )}

          <Dialog open={isOpen} onOpenChange={setIsOpen}>
            {/* Create Button */}
            {!(role === "admin" && selectedOrg === "ALL") && (
              <DialogTrigger asChild>
                <Button onClick={handleCreate}>+ Create</Button>
              </DialogTrigger>
            )}

            <DialogContent
              ref={dialogRef}
              className="max-h-[750px] sm:max-w-[1100px] overflow-y-auto dark:bg-sky-950"
              onCloseAutoFocus={(e) => {
                e.preventDefault();
                setIsModalDisabled(false);
              }}
            >
              <DialogHeader>
                <div className="flex justify-between items-center">
                  <DialogTitle>
                    {editingIncident
                      ? "Edit Incident Report"
                      : "File Incident Report"}
                  </DialogTitle>
                  {editingIncident && (
                    <Button
                      type="button"
                      variant="outline"
                      onClick={handlePrint}
                      disabled={isPrinting}
                      className="h-[34px]"
                    >
                      {isPrinting ? "Preparing..." : "Print"}
                    </Button>
                  )}
                </div>
              </DialogHeader>

              <IncidentModal
                onClose={handleClose}
                form={{
                  ...form,
                  organization: editingIncident
                    ? form.organization
                    : selectedOrg,
                }}
                setForm={setForm}
                editingId={editingIncident?._id}
                defaultForm={defaultForm}
              />
            </DialogContent>
          </Dialog>
        </div>

        {/* Main Table */}
        {isLoading && <p>Loading incidents...</p>}
        {isError && <p className="text-red-500">Failed to load incidents</p>}
        {incidents && (
          <IncidentMainTable
            editingIncident={editingIncident}
            data={incidents.data}
            onRowClick={handleRowClick}
            currentPage={incidents.page}
            totalPages={incidents.totalPages}
            itemsPerPage={itemsPerPage}
            onPageChange={setCurrentPage}
            onItemsPerPageChange={(val) => {
              setItemsPerPage(val);
              setCurrentPage(1);
            }}
          />
        )}
      </div>
    </div>
  );
}
